@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
body {
  overflow: hidden;
  margin: 0;
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/** style of Learn More button **/
.btn-learnmore {
  width: 28vh;
  height: 9vh;
  margin-top: 4vh;
  padding-top: 1.5vh;
  padding-bottom: 1.5vh;

  background-color: #ffd602;
  border-color: white;
  border-width: 3px;
  border-style: solid;

  font-size: 24px;
  color: white;
}

/** when you hover over button what happens **/
.btn-learnmore:hover {
  background-color: rgba(0, 0, 0, 0.03);
  border-color: rgba(255, 255, 255, 0.7);
}

/** this div surrounds the whole homge page (i.e. everything on screen besides navbar **/
.home {
  min-height: 90vh !important;
  max-height: 90vh !important;
  background-image: url(/app/static/media/Couchplane.9b394f0d.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-position-y: -14vh;
}

/** this is the div that surrounds "hey" and the learn more button **/
.text-modal {
  text-align: left;
  line-height: 100%;
  width: 52vh;
  padding-top: 14vh;
  margin-left: 9vh;

  font-size: 55px;
  font-weight: bold;
  color: white;
}

.container {
  min-height: 10vh !important;
  max-height: 10vh !important;
}

.primary-nav {
  display: flex;
  list-style-type: none;
  padding: 0;
  justify-content: flex-end;
}

.nav-item {
  margin-top: 2.7vh;
  margin-left: 4.5vh;
  margin-right: 4.5vh;

  position: relative;

  font-size: 20px;
  font-weight: bold;
}

.nav-item-active {
  margin-top: 2vh;
  margin-left: 4.5vh;
  margin-right: 4.5vh;
  margin-bottom: 1vh;

  position: relative;

  font-size: 20px;
  font-weight: bold;

  border-bottom: 3px solid #df4b31;
}

.nav-item:hover {
  margin-top: 2vh;
}

.nav-logo {
  margin-left: 8vh;
  margin-right: auto;
  position: relative;
}

img {
  width: 130%;
  height: auto;
  margin-top: 1vh;
}

a,
a:visited {
  text-decoration: none;
  color: black;
}

.App {
  text-align: center;
  min-height: 100vh;
  max-height: 100vh;
}

